import React, { Component } from "react";
import DownloadButton from "./components/DownloadButton";
import { observer } from "mobx-react";
import ModComponent, { Mod } from "./components/Mod";
import { action, IObservableValue, observable } from "mobx";
import FloorComponent, { Floor } from "./components/Floor";
import "./styles/style.scss";
import TabManager from "./components/TabManager";
import { buildingMaterials, stats } from "./data/defs";
import { About } from "./components/About";

export class FloorStore {
    @observable floors = observable.array<Floor>([]);
    @action addFloor(floor: Floor): void {
        this.floors.push(floor);
    }
    @action removeFloor(id: string): void {
        const floor = this.floors.find((f) => f.id === id);
        if (floor) this.floors.remove(floor);
    }

    constructor() {
        this.floors.push(new Floor());
    }
}

export const mod = new Mod();
export const floors = new FloorStore();
export const submitting = observable.box(false);

@observer
export default class StuffedFloorModGenerator extends Component {
    @observable mod: Mod = mod;
    @observable floors: FloorStore = floors;
    @observable tab: string = "about";
    @observable submitting: IObservableValue<boolean> = submitting;

    @action changeTab = (tab: string) => {
        this.tab = tab;
    };

    render() {
        const floor = this.floors.floors.find((f) => f.id === this.tab);
        return (
            <div id="main" className="container">
                <div className="box">
                    <TabManager
                        floors={this.floors}
                        onTabChange={this.changeTab}
                        tab={this.tab}
                    ></TabManager>
                    {this.tab === "about" && (
                        <About changeTab={this.changeTab} />
                    )}
                    {this.tab === "mod" && (
                        <ModComponent mod={this.mod}></ModComponent>
                    )}
                    {floor && <FloorComponent floor={floor}></FloorComponent>}
                </div>

                <datalist id="building-materials-list">
                    {buildingMaterials.map((m, i) => (
                        <option key={i}>{m.def}</option>
                    ))}
                </datalist>

                <datalist id="stats-list">
                    {stats.map((s, i) => (
                        <option key={i}>{s.def}</option>
                    ))}
                </datalist>

                {(this.tab === "mod" || floor) && (
                    <DownloadButton
                        about={this.mod}
                        floors={this.floors}
                    ></DownloadButton>
                )}
            </div>
        );
    }
}
