import React, { Component } from "react";
import { saveAs } from "file-saver";
import { observer } from "mobx-react";
import { action, computed, observable } from "mobx";
import { FloorStore, submitting } from "../StuffedFloorModGenerator";
import { Mod } from "./Mod";
import { BuildMod } from "../mod-builder";
import classNames from "classnames";
import isEmpty from "lodash/isEmpty";
import { errorArray } from "../utils";
import ErrorList from "./ErrorList";

interface DownloadButtonProps {
    floors: FloorStore;
    about: Mod;
}

@observer
export default class DownloadButton extends Component<DownloadButtonProps> {
    @observable building = false;
    @observable touched = false;
    @computed get errors() {
        const errors = [];
        const { about, floors } = this.props;
        if (!isEmpty(about.validationErrors)) {
            errors.push({
                label: "About",
                errors: errorArray(about.validationErrors),
            });
        }
        errors.push(
            ...floors.floors
                .filter((floor) => !isEmpty(floor.validationErrors))
                .map((floor) => ({
                    label: floor.label || floor.id,
                    errors: errorArray(floor.validationErrors),
                }))
        );

        return errors;
    }

    @action
    download = async () => {
        this.touched = true;
        const { floors, about } = this.props;
        if (this.errors.length) {
            submitting.set(true);
            return;
        } else {
            this.building = true;
        }
        const archive = await BuildMod(about, floors);
        saveAs(archive, `${this.props.about.name}.zip`);
        this.building = false;
    };

    render() {
        return (
            <div className="flex top">
                <button
                    className={classNames({
                        button: true,
                        "is-large": true,
                        "is-primary": true,
                        "is-loading": this.building,
                    })}
                    onClick={this.download}
                >
                    Download Mod
                </button>
                {this.touched &&
                    this.errors.map((error, index) => (
                        <ErrorList {...error} key={index} />
                    ))}
            </div>
        );
    }
}
