import { observer } from "mobx-react";
import React, { Component } from "react";
import Input from "./Input";
import { Switch } from "./Switch";
import { TextureProps } from "./TexturePreview";

@observer
export class TextureSize extends Component<TextureProps> {
    render() {
        const { texture } = this.props;
        const { settings, toggleSetting, updateSetting } = texture;
        return (
            <div className="texture-size">
                <span className="help">
                    Texture size:{" "}
                    {`${texture.raw.size.width} x ${texture.raw.size.height} (original)`}
                    {settings.tile &&
                        `, ${texture.raw.size.width * settings.tilesX} x ${
                            texture.raw.size.height * settings.tilesY
                        } (tiled)`}
                    {`, 1024 x 1024 (output)`}
                </span>
                <div className="columns" style={{ alignItems: "center" }}>
                    <div className="column">
                        <Switch
                            id="texture-tiling"
                            label="Tile"
                            small={true}
                            rounded={true}
                            outlined={true}
                            checked={settings.tile}
                            onChange={() => toggleSetting("tile")}
                            help={
                                <div>
                                    <p>
                                        Repeat the source texture multiple times
                                        to make individual patterns smaller, or
                                        to stop non-square textures getting
                                        stretched too badly.
                                    </p>
                                    <img
                                        src="https://i.imgur.com/pV5R48G.gif"
                                        alt="repeating example"
                                    />
                                </div>
                            }
                        />
                    </div>
                    {settings.tile && (
                        <>
                            <div className="column">
                                <Input
                                    id="texture-tiling-x"
                                    label="x"
                                    small={true}
                                    element={
                                        <input className="input is-small" />
                                    }
                                    value={settings.tilesX}
                                    onChange={(ev) =>
                                        updateSetting(
                                            "tilesX",
                                            parseInt(ev.target.value) || 1
                                        )
                                    }
                                />
                            </div>
                            <div className="column">
                                <Input
                                    id="texture-tiling-y"
                                    label="y"
                                    small={true}
                                    element={
                                        <input className="input is-small" />
                                    }
                                    value={settings.tilesY}
                                    onChange={(ev) =>
                                        updateSetting(
                                            "tilesY",
                                            parseInt(ev.target.value) || 1
                                        )
                                    }
                                />
                            </div>
                        </>
                    )}
                </div>
            </div>
        );
    }
}
