import { observer } from "mobx-react";
import React, { Component } from "react";
import Select from "react-select";
import {
    stuffCategories,
    researchDefs,
    burnedFloorDefs,
    affordances,
    tags,
} from "../data/defs";
import { edgeTypes } from "../data/enums";
import CostList from "./CostList";
import { FloorProps } from "./Floor";
import StatList from "./StatList";
import { StringList } from "./StringList";
import Input from "./Input";
import { StringWithDefaults } from "./StringWithDefaults";
import Help from "./Help";

@observer
export class FloorSettings_Detail extends Component<FloorProps> {
    render() {
        const { floor } = this.props;
        const { validationErrors: errors } = floor;
        return (
            <div className="settings-detail">
                <StringList
                    label="Stuff Categories"
                    id={`${floor.id}-stuff-categories`}
                    options={stuffCategories}
                    selected={floor.stuffCategories}
                    errors={errors.stuffCategories}
                    help="Stuff categories that this floor can be made of. Vanilla categories are listed as options, categories added by other mods can be added by typing the defName."
                ></StringList>
                <Input
                    label="Stuff Cost"
                    value={floor.stuffcost}
                    id={`${floor.id}-stuff-cost`}
                    onChange={(e) =>
                        (floor.stuffcost = parseInt(e.target.value) || 0)
                    }
                    error={errors.stuffcost}
                    help="Amount of stuff needed to build the floor. The cost is automatically increased (x20) for 'precious' materials such as gold, silver and jade."
                />
                <CostList
                    {...floor}
                    errors={floor.validationErrors.costs}
                    help="Other ingredients needed to build the floor, for example extra components or steel. DefNames of vanilla items are listed as options, you can use modded items by typing in the correct defName."
                ></CostList>
                <StatList
                    {...floor}
                    errors={floor.validationErrors.stats}
                    help={
                        <div>
                            <b>Stats used and/or provided by this floor.</b>
                            <br />
                            Note that while all vanilla in-game stats are listed
                            as options, only a small subset is actually used in
                            the game that I'm aware off;
                            <ul>
                                <li>Beauty</li>
                                <li>CostToBuild</li>
                                <li>Flammability</li>
                                <li>Cleanliness</li>
                            </ul>
                            <br />
                            <b>Interactions with Stuff</b>
                            <br />
                            The stats given for the floor are further affected
                            by the particular stuff used. For example, all types
                            of stone have a <i>considerably higher</i>{" "}
                            CostToBuild offset. This means that the CostToBuild
                            stat on the floor should probably be lower, to
                            balance it with other types of floors.
                        </div>
                    }
                ></StatList>
                <StringList
                    label="Research Prerequisites"
                    id={`${floor.id}-research-prerequisites`}
                    options={researchDefs.map((d) => d.def)}
                    selected={floor.researchPrerequisites}
                    errors={errors.researchPrerequisites}
                    help="Research that needs to be completed before this floor can be built. Type in a defName to add modded research."
                ></StringList>
                <StringWithDefaults
                    label="Burned Def"
                    id={`${floor.id}-burned-def`}
                    selected={floor.burnedDef || ""}
                    options={burnedFloorDefs}
                    error={errors.burnedDef}
                    updateSelected={(value) => (floor.burnedDef = value)}
                    help="Remains left after a floor burns. Only relevant for floors that have a Flammability > 0"
                />
                <div className="field">
                    <label
                        htmlFor={`${floor.id}-edge-type`}
                        className="label flex"
                    >
                        Edge Type
                        <Help>
                            <span>
                                Edge type determines the way different floors
                                blend into eachother.
                                <br /> Should be set to 'Hard' for most types of
                                floor, but e.g. dirt-based floors might benefit
                                from a softer transition.
                            </span>
                        </Help>
                    </label>
                    <Select
                        id={`${floor.id}-edge-type`}
                        defaultValue={{
                            label: floor.edgeType as string,
                            value: floor.edgeType as string,
                        }}
                        onChange={(_option) => {
                            const option = _option as option;
                            floor.edgeType = option.value as EdgeType;
                        }}
                        options={edgeTypes.map((edge) => ({
                            label: edge,
                            value: edge,
                        }))}
                        className="react-select"
                        classNamePrefix=" "
                    ></Select>
                </div>
                <StringWithDefaults
                    label="Affordance Needed"
                    id={`${floor.id}-affordance-needed`}
                    selected={floor.terrainAffordanceNeeded}
                    options={affordances}
                    updateSelected={(value) =>
                        (floor.terrainAffordanceNeeded = value as Affordance)
                    }
                    error={errors.terrainAffordanceNeeded}
                    help="What type of ground can this floor can be built on. Heavy equates to solid ground, medium and light to increasingly soft, unstable and/or muddy ground."
                />
                <StringList
                    label="Affordances"
                    id={`${floor.id}-affordances`}
                    options={affordances}
                    selected={floor.affordances}
                    errors={errors.affordances}
                    help="What type of things can this floor support? See also 'Affordance Needed'"
                />
                <StringList
                    label="Tags"
                    id={`${floor.id}-tags`}
                    options={tags}
                    selected={floor.tags}
                    errors={errors.tags}
                    help="Tags are used to determine what pawns see as a floor 'Floor', or what royals consider good enough 'FineFloor'. Custom tags can also be entered, which may be useful for other mods."
                ></StringList>
            </div>
        );
    }
}
