import React, { Component } from "react";
import { Switch } from "./Switch";
import { TextureProps } from "./TexturePreview";
import Select from "react-select";
import { observer } from "mobx-react";
import { computed } from "mobx";
import { FloorProps } from "./Floor";
import { stuffs } from "../data/defs";
import flatten from "lodash/flatten";

@observer
export default class TextureStuff extends Component<TextureProps & FloorProps> {
    @computed get options() {
        const { floor } = this.props;
        return flatten(
            floor.stuffCategories.map((cat) =>
                stuffs[cat].map((s) => ({ label: s.label, value: s.color }))
            )
        );
    }

    render() {
        const { settings, toggleSetting, updateSetting } = this.props.texture;
        return (
            <div className="columns" style={{ alignItems: "center" }}>
                <div className="column is-4">
                    <Switch
                        id="texture-stuff-preview"
                        label="Preview stuff colour"
                        checked={settings.preview}
                        onChange={() => toggleSetting("preview")}
                    />
                </div>
                {settings.preview && (
                    <>
                        <div className="column is-1">
                            <div
                                className="colour-preview"
                                style={{
                                    backgroundColor: settings.previewColor,
                                }}
                            ></div>
                        </div>
                        <div className="column is-7">
                            <Select
                                id="texture-stuff-preview-stuff"
                                value={this.options.find(
                                    (o) => o.value === settings.previewColor
                                )}
                                defaultValue={this.options[0]}
                                onChange={(o) => {
                                    console.log({ o });
                                    updateSetting(
                                        "previewColor",
                                        (o as option).value
                                    );
                                }}
                                options={this.options}
                                className="react-select is-small"
                                classNamePrefix=" "
                            ></Select>
                        </div>
                    </>
                )}
            </div>
        );
    }
}
