import { observer } from "mobx-react";
import React, { Component } from "react";
import { FloorProps } from "./Floor";
import Input from "./Input";

@observer
export class FloorBasics extends Component<FloorProps> {
    render() {
        const { floor } = this.props;
        const { validationErrors: errors } = floor;

        return (
            <>
                <Input
                    label="Label"
                    error={errors.label}
                    id={`${floor.id}-label`}
                    value={floor.label}
                    onChange={(e) => (floor.label = e.target.value)}
                    help="Label of the floor. Note that in-game floors will get their stuff prepended, e.g. 'Tiles' will become 'Jade Tiles', and so forth."
                />
                <Input
                    label="Description"
                    id={`${floor.id}-desc`}
                    value={floor.description}
                    onChange={(e) => (floor.description = e.target.value)}
                    error={errors.description}
                    element={<textarea className="textarea" />}
                    help="Description of the floor as shown in the game's info pages and on the architect tab."
                />
            </>
        );
    }
}
