import React, { Component } from "react";
import { Error } from "./Error";

interface ErrorListProps {
    label: string;
    errors: string[];
}

export default class ErrorList extends Component<ErrorListProps> {
    render() {
        const { label, errors } = this.props;
        return (
            <div className="error-list ml-3 p1">
                <h5 className="is-5 title ml-3">{label}</h5>
                {errors.map((error, index) => (
                    <Error touched error={error} key={index} />
                ))}
            </div>
        );
    }
}
