import React, { Component } from "react";
import { observer } from "mobx-react";
import { computed, observable } from "mobx";
import { sanitizeString, unwrapErrors } from "../utils";
import * as yup from "yup";
import Input from "./Input";
import PreviewImage from "./PreviewImage";

export interface ModProps {
    mod: Mod;
}

export class Mod {
    @observable name: string = "";
    @observable description: string = "";
    @observable author: string = "";
    @observable url: string = "";

    @computed get packageId() {
        return sanitizeString(this.author) + "." + sanitizeString(this.name);
    }

    @computed get validationErrors() {
        console.log("updating errors");
        return unwrapErrors<Mod>(this, ModSchema);
    }
}

export const ModSchema = yup.object({
    name: yup.string().required(),
    description: yup.string().required(),
    author: yup.string().required(),
    url: yup.string().url(),
    packageId: yup.string().required(),
});

@observer
export default class ModComponent extends Component<ModProps> {
    render() {
        const {
            name,
            description,
            author,
            url,
            validationErrors: errors,
        } = this.props.mod;
        return (
            <div id="about">
                <div className="columns">
                    <div className="column">
                        <Input
                            label="Mod Name"
                            id="mod.name"
                            value={name}
                            onChange={(e) =>
                                (this.props.mod.name = e.target.value)
                            }
                            error={errors.name}
                            help="Mod name as shown in game"
                        />
                        <Input
                            label="Mod Description"
                            id="mod.desc"
                            value={description}
                            onChange={(e) =>
                                (this.props.mod.description = e.target.value)
                            }
                            error={errors.description}
                            element={<textarea className="textarea" />}
                            help="Mod description as shown in game"
                        />
                        <Input
                            label="Author"
                            id="mod.author"
                            value={author}
                            onChange={(e) =>
                                (this.props.mod.author = e.target.value)
                            }
                            error={errors.author}
                            help="Author name as shown in game"
                        />
                        <Input
                            label="Link"
                            id="mod.url"
                            value={url}
                            onChange={(e) =>
                                (this.props.mod.url = e.target.value)
                            }
                            error={errors.url}
                            help="Link to a page where players can find more information about your mod. This is not required."
                        />
                    </div>
                    <div className="column">
                        <PreviewImage />
                    </div>
                </div>
            </div>
        );
    }
}
