import { observer } from "mobx-react";
import React, { Component } from "react";
import Select from "react-select";
import { simpleMode_materials, simpleMode_type } from "../data/defaults";
import { FloorProps } from "./Floor";
import Help from "./Help";

export interface SimpleSettings {
    material: Material;
    type: Type;
}

export interface SimpleSettingsProps extends FloorProps {
    settings: SimpleSettings;
    setMaterial: (mat: Material) => void;
    setType: (type: Type) => void;
}

@observer
export class FloorSettings_Simple extends Component<SimpleSettingsProps> {
    render() {
        const { settings, floor, setMaterial, setType } = this.props;
        return (
            <div className="settings-simple">
                <div className="field">
                    <label
                        htmlFor={`${floor.id}-simple-material`}
                        className="label flex"
                    >
                        Material
                        <Help title="Main flooring material. Individual floors will be generated for all of the types of this material."></Help>
                    </label>
                    <Select
                        id={`${floor.id}-simple-material`}
                        value={{
                            label: settings.material,
                            value: settings.material,
                        }}
                        onChange={(_option) => {
                            const option = _option as option;
                            setMaterial(option.value as Material);
                        }}
                        options={simpleMode_materials.map((mat) => ({
                            label: mat,
                            value: mat,
                        }))}
                        className="react-select"
                        classNamePrefix=" "
                    ></Select>
                </div>
                <div className="field">
                    <label
                        htmlFor={`${floor.id}-simple-type`}
                        className="label flex"
                    >
                        Beauty
                        <Help title="Beauty determines the beauty, material cost and amount of effort needed to build the floor. Royal floors are the only floors considered good enough by useless gits  esteemed empire officials"></Help>
                    </label>
                    <Select
                        id={`${floor.id}-simple-type`}
                        defaultValue={{
                            label: settings.type,
                            value: settings.type,
                        }}
                        onChange={(_option) => {
                            const option = _option as option;
                            setType(option.value as Type);
                        }}
                        options={simpleMode_type.map((type) => ({
                            label: type,
                            value: type,
                        }))}
                        className="react-select"
                        classNamePrefix=" "
                    ></Select>
                </div>
            </div>
        );
    }
}
