import { action, observable } from "mobx";
import { observer } from "mobx-react";
import React, { Component } from "react";
import { FloorProps } from "./Floor";
import { FloorSettings_Detail } from "./FloorSettings_Detail";
import { FloorSettings_Simple, SimpleSettings } from "./FloorSettings_Simple";
import Help from "./Help";

@observer
export class FloorSettings extends Component<FloorProps> {
    @observable detailed: boolean = false;
    @observable simple: SimpleSettings = {
        material: "Stone",
        type: "Pretty",
    };

    @action toggleDetailed = () => {
        this.detailed = !this.detailed;

        if (!this.detailed)
            this.props.floor.resetFloor(this.simple.material, this.simple.type);
    };

    @action setMaterial = (material: Material) => {
        this.simple.material = material;
        this.props.floor.resetFloor(material, this.simple.type);
    };

    @action setType = (type: Type) => {
        this.simple.type = type;
        this.props.floor.resetFloor(this.simple.material, type);
    };

    render() {
        const { floor } = this.props;
        const settings = this.detailed ? (
            /* eslint-disable react/jsx-pascal-case */
            <FloorSettings_Detail floor={floor}></FloorSettings_Detail>
        ) : (
            <FloorSettings_Simple
                floor={floor}
                settings={this.simple}
                setMaterial={this.setMaterial}
                setType={this.setType}
            ></FloorSettings_Simple>
            /* eslint-enable react/jsx-pascal-case */
        );

        return (
            <div className="settings">
                <div className="field flex">
                    <input
                        id={`${floor.id}-settings-detailed`}
                        type="checkbox"
                        className="switch is-rounded is-outlined is-small"
                        checked={this.detailed}
                        onChange={this.toggleDetailed}
                    />
                    <label htmlFor={`${floor.id}-settings-detailed`}>
                        advanced mode
                    </label>
                    <Help>
                        <div>
                            <b>Basic Mode:</b>
                            <p>
                                You can change the main material (stuff
                                category) and choose a beauty level. This sets
                                values for material costs, beauty amounts and
                                build times that are (roughly) balanced with
                                those in the vanilla game and royalty expansion.
                            </p>
                            <br />
                            <b>Advanced Mode:</b>
                            <p>
                                Advanced mode lets you pick, choose and tune all
                                the settings. See individual settings for more
                                details.
                            </p>
                        </div>
                    </Help>
                </div>
                {settings}
            </div>
        );
    }
}
