import React, { Component } from "react";

interface AboutProps {
    changeTab: (tab: string) => void;
}

export class About extends Component<AboutProps> {
    render() {
        return (
            <div className="about content">
                <h1 className="title is-3">Stuffed Floor Generator</h1>
                <h2 className="title is-5">What is this?</h2>
                <p>
                    This is a webapp that lets you create your own stuffed
                    floors for RimWorld quickly and easily.
                </p>
                <h2 className="title is-5">How does it work?</h2>
                <p>
                    You find a texture, and put it into the app. You can then
                    finetune the texture to make it look great. Give it a name
                    and description, and the app takes care of filling out the
                    details and generating all the XML code.
                </p>
                <p>
                    You can also take full control, and tailor your floors to
                    your precise needs.
                </p>
                <p className="call-to-action">Just go give it a try!</p>
                <h2 className="title is-5">
                    Can I share the floors on the Workshop?
                </h2>
                <p>
                    Yes, absolutely! I've even included a preview generator so
                    that you'll have a decent preview without any additional
                    effort.
                </p>
                <p>
                    There's one big caveat: <b>you have to respect copyright</b>
                    .
                </p>
                <p>
                    By using this app, you agree that you understand that you're
                    not allowed to distribute copyrighted images as your own
                    textures, and that I'm not responsible for anything you
                    share that was created with this app.
                </p>
                <button
                    className="button is-large is-primary"
                    onClick={() => this.props.changeTab("mod")}
                >
                    Lets get started!
                </button>
            </div>
        );
    }
}
