import React, { Component } from "react";
import { Tooltip } from "@material-ui/core";
import InfoIcon from "@material-ui/icons/Info";
import PlayCircleFilledIcon from "@material-ui/icons/PlayCircleFilled";

interface BaseHelpProps {
    video?: boolean;
    fontSize?: "inherit" | "default" | "small" | "large";
}

interface TextHelpProps extends BaseHelpProps {
    title: string | React.ReactElement<any, any>;
    children?: never;
}

interface RichHelpProps extends BaseHelpProps {
    children: React.ReactElement<any, any>;
    title?: never;
}

type HelpProps = TextHelpProps | RichHelpProps;

export default class Help extends Component<HelpProps> {
    render() {
        const { video, title, children, fontSize = "small" } = this.props;
        return (
            <Tooltip
                title={title || children!}
                placement="right-end"
                arrow
                interactive
            >
                <span className="flex pl-2">
                    {video && <PlayCircleFilledIcon fontSize={fontSize} />}
                    {!video && <InfoIcon fontSize={fontSize} />}
                </span>
            </Tooltip>
        );
    }
}
