import { merge } from "lodash";
import { action, observable } from "mobx";
import { observer } from "mobx-react";
import React, { Component, createRef } from "react";
import { mod, floors, FloorStore } from "../StuffedFloorModGenerator";
import { elementSize, waitForFontLoad } from "../utils";
import { Floor } from "./Floor";

const PREVIEW_SIZE = {
    width: 640,
    height: 380,
};
const TILE_SIZE = 1024 / 16;
const ANGLE = -0.1;

@observer
export default class PreviewImage extends Component {
    @observable floors = floors;
    @observable about = mod;
    canvasRef = createRef<HTMLCanvasElement>();
    @action async renderCanvas() {
        const canvas = this.canvasRef.current;
        if (!canvas) throw new Error("canvas ref not bound!");
        const ctx = canvas.getContext("2d");
        if (!ctx) throw new Error("could not get rendering context");

        await waitForFontLoad("40pt Staatliches");

        const availableSize = elementSize(canvas.parentElement!);
        canvas.width = Math.min(PREVIEW_SIZE.width, availableSize.width);
        const scale = canvas.width / PREVIEW_SIZE.width;
        canvas.height = PREVIEW_SIZE.height * scale;
        ctx.scale(scale, scale);

        // draw background
        ctx.fillStyle = "#222222";
        ctx.fillRect(0, 0, PREVIEW_SIZE.width, PREVIEW_SIZE.height);

        const bannerSettings = merge({}, defaultBannerSettings, {
            width: 620,
            bannerHeight: 60,
            pointOffset: { x: 100, y: 30 },
            bannerOffset: { x: 10, y: 220 },
        });
        drawBannerBackground(ctx, bannerSettings);

        // draw textures
        await drawTextures(floors, ctx);

        // draw banner
        drawBannerForeground(ctx, bannerSettings);
        drawBannerTitle(mod.name, ctx, bannerSettings);
    }

    componentDidUpdate = this.renderCanvas;
    componentDidMount = this.renderCanvas;

    render() {
        /* eslint-disable @typescript-eslint/no-unused-vars */
        const { name } = this.about;
        const floors = this.floors;
        /* eslint-enable */
        return (
            <div className="preview">
                <span className="label">Preview</span>
                <canvas ref={this.canvasRef} data-mod />
            </div>
        );
    }
}

async function drawTextures(floors: FloorStore, ctx: CanvasRenderingContext2D) {
    const floorCount = floors.floors.length;
    const cols = Math.ceil(Math.sqrt(floorCount));
    const rows = Math.ceil(floorCount / cols); // sounds the same as cols, but isn't. sqrt(2) = 1.4 -> 2 cols, 1 row
    const floorSize = {
        width: (PREVIEW_SIZE.width - 30) / cols,
        height: (PREVIEW_SIZE.height - 10) / rows,
    };

    for (let i = 0; i < floorCount; i++) {
        await drawFloorPreview(
            floors.floors[i],
            i % cols,
            Math.floor(i / cols),
            floorSize,
            floorCount > 1,
            ctx
        );
    }
}

async function drawFloorPreview(
    floor: Floor,
    col: number,
    row: number,
    size: size,
    label: boolean,
    ctx: CanvasRenderingContext2D
) {
    const tl = { x: col * size.width + 30, y: row * size.height + 10 };
    size = { width: size.width - 10, height: size.height - 10 };
    // ctx.strokeStyle = "red";
    // ctx.strokeRect(tl.x, tl.y, size.width, size.height);

    ctx.fillStyle = "#fff";
    const titleSize = label ? 20 : 0;

    // draw label
    if (label) {
        ctx.font = `${titleSize}px Staatliches`;
        ctx.textBaseline = "top";
        ctx.textAlign = "center";
        ctx.fillText(
            floor.label || floor.id,
            tl.x + size.width / 2,
            tl.y + 4,
            size.width
        );
    }

    // draw previews
    const texture = floor.texture?.out?.texture;
    if (!texture) return;
    const textureData = await createImageBitmap(floor.texture?.out?.texture!);
    const stuffCount = floor.stuffs.length;
    let previewSize = Math.min(
        size.height - titleSize,
        size.width / Math.sqrt(stuffCount)
    );
    const angleOffset = Math.abs(previewSize * Math.tan(ANGLE));
    if (previewSize + angleOffset > size.height - titleSize) {
        previewSize = size.height - titleSize - angleOffset;
    }
    const rows = Math.floor(size.height / (previewSize * 0.66));
    const cols = Math.ceil(stuffCount / rows);
    const previewOffset = {
        x: cols > 1 ? (size.width - angleOffset - previewSize) / (cols - 1) : 0,
        y:
            rows > 1
                ? (size.height - angleOffset - previewSize) / (rows - 1)
                : 0,
    };
    const previewTiles = Math.ceil(previewSize / TILE_SIZE);

    for (let i = 0; i < stuffCount; i++) {
        const col = i % cols;
        const row = Math.floor(i / cols);
        const pos = {
            x: tl.x + col * previewOffset.x,
            y: tl.y + angleOffset + row * previewOffset.y + titleSize,
        };

        ctx.translate(pos.x, pos.y);
        ctx.rotate(ANGLE);

        // for each stuff, create a mini preview.
        // render center x tiles of texture, depending on preview size
        ctx.globalCompositeOperation = "source-over";
        ctx.shadowColor = "rgba(0,0,0,.5)";
        ctx.shadowBlur = 5;
        ctx.shadowOffsetX = 3;
        ctx.shadowOffsetY = 3;
        ctx.drawImage(
            textureData,
            ((16 - previewTiles) / 2) * TILE_SIZE,
            ((16 - previewTiles) / 2) * TILE_SIZE,
            previewTiles * TILE_SIZE,
            previewTiles * TILE_SIZE,
            0,
            0,
            previewSize,
            previewSize
        );

        // overlay stuff colour
        ctx.globalCompositeOperation = "multiply";
        ctx.shadowColor = "transparent";
        ctx.fillStyle = floor.stuffs[i].color;
        ctx.fillRect(0, 0, previewSize, previewSize);

        ctx.rotate(-ANGLE);
        ctx.translate(-pos.x, -pos.y);
    }

    ctx.globalCompositeOperation = "source-over";
}

function drawBannerTitle(
    title: string,
    ctx: CanvasRenderingContext2D,
    _settings: BannerSettings
) {
    // draw title
    ctx.fillStyle = _settings.colours.text;
    ctx.font = `${_settings.bannerHeight}px Staatliches`;
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillText(
        title || "Your Mod Name",
        _settings.width / 2 +
            _settings.bannerOffset.x +
            _settings.titleOffset.x,
        _settings.margin +
            _settings.titleOffset.y +
            _settings.pointOffset.y +
            _settings.bannerOffset.y +
            _settings.bannerHeight / 2,
        _settings.width - (_settings.margin + _settings.bannerSlant) * 2
    );
    ctx.font = `20px Staatliches`;
    ctx.textBaseline = "top";
    ctx.fillText(
        "A Stuffed Floors mod",
        _settings.width / 2 +
            _settings.bannerOffset.x +
            _settings.titleOffset.x,
        _settings.margin +
            _settings.titleOffset.y +
            _settings.pointOffset.y +
            _settings.bannerOffset.y +
            _settings.bannerHeight,
        _settings.width - (_settings.margin + _settings.bannerSlant) * 2
    );
}

function drawBannerForeground(
    ctx: CanvasRenderingContext2D,
    _settings: BannerSettings
) {
    const POINTS = {
        topLeft: {
            x: _settings.margin + _settings.bannerOffset.x,
            y:
                _settings.margin +
                _settings.bannerOffset.y +
                _settings.pointOffset.y,
        },
        topRight: {
            x:
                _settings.width +
                _settings.bannerOffset.x -
                _settings.margin -
                _settings.bannerSlant,
            y:
                _settings.margin +
                _settings.bannerOffset.y +
                _settings.pointOffset.y,
        },
        bottomLeft: {
            x:
                _settings.margin +
                _settings.bannerOffset.x +
                _settings.bannerSlant,
            y:
                _settings.margin +
                _settings.bannerHeight +
                _settings.bannerOffset.y +
                _settings.pointOffset.y,
        },
        bottomRight: {
            x: _settings.width + _settings.bannerOffset.x - _settings.margin,
            y:
                _settings.margin +
                _settings.bannerHeight +
                _settings.bannerOffset.y +
                _settings.pointOffset.y,
        },
    };

    // draw banner foreground
    ctx.fillStyle = _settings.colours.bannerFg;
    createPath(
        ctx,
        POINTS.topLeft,
        POINTS.topRight,
        POINTS.bottomRight,
        POINTS.bottomLeft
    );
    ctx.fill();
}

function drawBannerBackground(
    ctx: CanvasRenderingContext2D,
    _settings: BannerSettings
) {
    const POINTS = {
        topLeft: {
            x: _settings.margin + _settings.bannerOffset.x,
            y:
                _settings.margin +
                _settings.bannerOffset.y +
                _settings.pointOffset.y,
        },
        topRight: {
            x:
                _settings.width +
                _settings.bannerOffset.x -
                _settings.margin -
                _settings.bannerSlant,
            y:
                _settings.margin +
                _settings.bannerOffset.y +
                _settings.pointOffset.y,
        },
        bottomLeft: {
            x:
                _settings.margin +
                _settings.bannerOffset.x +
                _settings.bannerSlant,
            y:
                _settings.margin +
                _settings.bannerHeight +
                _settings.bannerOffset.y +
                _settings.pointOffset.y,
        },
        bottomRight: {
            x: _settings.width + _settings.bannerOffset.x - _settings.margin,
            y:
                _settings.margin +
                _settings.bannerHeight +
                _settings.bannerOffset.y +
                _settings.pointOffset.y,
        },
        pointLeft: {
            x:
                _settings.margin +
                _settings.bannerOffset.x +
                _settings.pointOffset.x,
            y: _settings.margin + _settings.bannerOffset.y,
        },
        pointRight: {
            x:
                _settings.width +
                _settings.bannerOffset.x -
                _settings.margin -
                _settings.pointOffset.x,
            y:
                _settings.margin +
                2 * _settings.pointOffset.y +
                _settings.bannerOffset.y +
                _settings.bannerHeight,
        },
    };

    // draw banner background pieces
    ctx.fillStyle = _settings.colours.bannerBg;
    createPath(ctx, POINTS.topLeft, POINTS.pointLeft, POINTS.bottomLeft);
    ctx.fill();
    createPath(ctx, POINTS.bottomRight, POINTS.pointRight, POINTS.topRight);
    ctx.fill();
}

function createPath(
    context: CanvasRenderingContext2D,
    ...points: { x: number; y: number }[]
): void {
    context.beginPath();
    context.moveTo(points[0].x, points[0].y);
    for (let i = 1; i < points.length; i++) {
        context.lineTo(points[i].x, points[i].y);
    }
    context.closePath();
}

export type point = { x: number; y: number };
export type colour = string;

export interface BannerSettings {
    width: number;
    margin: number;
    pointOffset: point;
    titleOffset: point;
    colours: {
        [label: string]: colour;
    };
    blueColours: {
        [label: string]: colour;
    };
    bannerOffset: point;
    bannerHeight: number;
    bannerSlant: number;
}

const defaultBannerSettings: BannerSettings = {
    width: 480,
    margin: 0,
    pointOffset: { x: 80, y: 10 },
    bannerOffset: { x: 0, y: 0 },
    titleOffset: { x: 0, y: 5 },
    colours: {
        bannerBg: "#762800",
        bannerFg: "#C7602C",
        text: "#fff",
    },
    blueColours: {
        bannerBg: "#145398",
        bannerFg: "#2c87e9",
        boxBg: "#1a222b",
        text: "#fff",
    },
    bannerHeight: 30,
    bannerSlant: 10,
};
